import { Sequelize, DataTypes } from 'sequelize'
import moment from 'moment'
import sequelize from './sequelize'

// 求职简历
const QiuzhijianliModel = sequelize.define('QiuzhijianliModel', {
	id: {
		type: DataTypes.BIGINT,
		primaryKey: true,
		autoIncrement: true,
		allowNull: false,
		comment: '主键id'
	},
	xuehao: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '学号'
	},
	xueshengxingming: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '学生姓名'
	},
	touxiang: {
		type: DataTypes.TEXT,
		defaultValue: '',
		allowNull: true,
		comment: '头像'
	},
	xingbie: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '性别'
	},
	nianling: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '年龄'
	},
	zhuanye: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '专业'
	},
	xueli: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '学历'
	},
	biyeyuanxiao: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '毕业院校'
	},
	waiyushuiping: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '外语水平'
	},
	qiuzhiyixiang: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '求职意向'
	},
	qiwangxinzi: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '期望薪资'
	},
	shehuishijian: {
		type: DataTypes.TEXT,
		defaultValue: '',
		allowNull: true,
		comment: '社会实践'
	},
	gerenjianjie: {
		type: DataTypes.TEXT,
		defaultValue: '',
		allowNull: true,
		comment: '个人简介'
	},
	userid: {
		type: DataTypes.BIGINT,
		defaultValue: 0,
		allowNull: true,
		comment: '用户id'
	},
	addtime: {
  		type: DataTypes.DATE,
  		defaultValue: DataTypes.NOW,
    	allowNull: false,
    	get() {
            return moment(this.getDataValue('addtime')).format('YYYY-MM-DD HH:mm:ss')
        },
		comment: '添加时间'
	}
}, {
	timestamps: false,
	freezeTableName: true,
	tableName: 'qiuzhijianli'
})

export default QiuzhijianliModel
